// Load a glTF resource
const promisifiedLoad = (loader, file) =>
  new Promise((resolve, reject) => {
    const onGLTFProgress = xhr => {
      if (xhr.total > 0) {
        const loadPercent = Math.ceil((xhr.loaded / xhr.total) * 100)
        console.log(`${loadPercent}% loaded`)
      }
    }

    loader.load(file, resolve, onGLTFProgress, reject)
  })

export const loadModel = async file => {
  if (!file) {
    throw new Error('loadModel needs { file } argument to be set.')
  }

  try {
    const { AnimationMixer } = await import('three/src/animation/AnimationMixer.js')
    const { GLTFLoader } = await import('./GLTFLoader.js')

    const gltf = await promisifiedLoad(new GLTFLoader(), file)

    const model = gltf.scene

    let mixer = null
    // start animations
    if (gltf.animations && gltf.animations.length) {
      mixer = new AnimationMixer(model)

      gltf.animations.forEach(anim => mixer.clipAction(anim).play())
    }

    return {
      model,
      mixer,
    }
  } catch (e) {
    throw e
  }
}
