export const loadReticle = async () => {
  const [{ MeshBasicMaterial }, { Mesh }, { RingBufferGeometry }] = await Promise.all([
    import('three/src/materials/MeshBasicMaterial.js'),
    import('three/src/objects/Mesh.js'),
    import('three/src/geometries/RingGeometry.js'),
  ])

  const reticleGeometry = new RingBufferGeometry(0.15, 0.2, 32).rotateX(-Math.PI / 2)
  const reticleMaterial = new MeshBasicMaterial()

  const reticle = new Mesh(reticleGeometry, reticleMaterial)

  reticle.matrixAutoUpdate = false
  reticle.visible = false

  return reticle
}
