let locationsGroupVisible = true
let objectsGroupVisible = false

export function onZoomEnd() {
  const { map, locationsGroup, objectsGroup } = this
  const zoom = map.getZoom()
  const center = map.getCenter()
  console.log({ zoom, center })

  if (zoom >= 17 && locationsGroupVisible) {
    locationsGroupVisible = false
    map.removeLayer(locationsGroup)
  } else if (!locationsGroupVisible) {
    locationsGroupVisible = true
    map.addLayer(locationsGroup)
  }

  if (zoom >= 17 && !objectsGroupVisible) {
    objectsGroupVisible = true
    map.addLayer(objectsGroup)
  } else if (objectsGroupVisible) {
    objectsGroupVisible = false
    map.removeLayer(objectsGroup)
  }
}
