import { clearThreeScene } from '../three/clearThreeScene.js'

export const endSession = async ({ renderer, scene }) => {
  if ('xr' in renderer) {
    const session = renderer.xr.getSession()

    if (session) {
      session.end()
    }
  }

  await clearThreeScene(scene)

  const canvas = document.getElementById('three-canvas')
  const parent = canvas.parentNode
  if (parent.id === 'three-container') {
    parent.removeChild(canvas)
  } else {
    parent.parentNode.removeChild(parent)
  }
}
