import { SCENE_TYPES } from '../constants.js'

export const spawnModel = ({ controller, model, reticle, scene, type }) => () => {
  // spawnModel has multiple callee forms,
  // depending on type there are different ways to spawn models

  if (type === SCENE_TYPES.HIT) {
    if (reticle.visible) {
      // we both have ar and ar found a hit position to place our model at
      model.position.setFromMatrixPosition(reticle.matrix)
      scene.add(model)
    }
  } else if (type === SCENE_TYPES.FLOAT) {
    const mesh = model.clone()
    // place the mesh half a meter in front of the controller
    mesh.position.set(0, 0, -1).applyMatrix4(controller.matrixWorld)
    mesh.quaternion.setFromRotationMatrix(controller.matrixWorld)
    scene.add(mesh)
  }
}
