// all data is being imported statically,
// for now... we only deal with vienna anyways.
import { data } from '../../data.js'

import * as config from '../../config.js'

import { addLocationsToLayer, locateControl } from './geo/index.js'
import { onZoomEnd } from './onZoomEnd.js'

import * as L from 'leaflet/dist/leaflet-src.esm.js'

export const initMap = async () => {
  const map = L.map('map-container', {
    ...config.map.options,
    maxBounds: data.bounds,
    zoom: data.zoom,
    center: [data.lat, data.lon],
  })

  // add the OpenStreetMap tiles from stamen
  const tileLayer = L.tileLayer(config.map.url, {
    attribution: config.map.attribution,
  })

  tileLayer.addTo(map)

  // show the scale bar on the lower left corner
  L.control.scale().addTo(map)

  const { locationsGroup, objectsGroup } = addLocationsToLayer(map)

  map.addLayer(locationsGroup)

  L.DomEvent.on(map, 'zoomend', onZoomEnd, { map, locationsGroup, objectsGroup })

  locateControl(config.locateControl).addTo(map)

  return { L, map }
}
