import { startSession } from './xr/startSession.js'
import { endSession } from './xr/endSession.js'

export const onClickStartScene = async object => {
  let xrSupported = false

  try {
    xrSupported = await navigator.xr.isSessionSupported('immersive-ar')
  } catch (e) {
    // we do not care, fallback is built in.
    console.error('xrSupported error', e)
  }

  let started = false
  let toggling = false
  let scene = false

  toggling = true

  try {
    if (!started) {
      scene = await startSession({ data: object, xrSupported })

      started = true
    } else {
      await endSession({ renderer, scene })

      started = false
    }
  } catch (e) {
    console.error(e)
  }

  toggling = false
}
