import * as L from 'leaflet/dist/leaflet-src.esm.js'

import { LocationMarker } from './LocationMarker.js'

export const CompassMarker = LocationMarker.extend({
  initialize(latlng, heading, options) {
    L.Util.setOptions(this, options)
    this._latlng = latlng
    this._heading = heading
    this.createIcon()
  },

  setHeading(heading) {
    this._heading = heading
  },

  /**
   * Create a styled arrow compass marker
   */
  _getIconSVG(options, style) {
    const r = options.radius
    const w = options.width + options.weight
    const h = (r + options.depth + options.weight) * 2
    const path = 'M0,0 l' + options.width / 2 + ',' + options.depth + ' l-' + w + ',0 z'
    const svgstyle = 'transform: rotate(' + this._heading + 'deg)'
    const svg = `
<svg width="${w}" height="${h}" viewBox="-${w / 2} 0 ${w} ${h}" style="${svgstyle}">
  <path d="${path} style="${style}" />
</svg>
`
      .trim()
      .replace(/\n/g, '')

    return {
      className: 'leaflet-control-locate-heading',
      svg: svg,
      w: w,
      h: h,
    }
  },
})
