import * as config from '../../config.js'

import {
  debugView,
  loadModel,
  loadReticle,
  onWindowResize,
  render,
  spawnModel,
} from '../three/index.js'

import { SCENE_TYPES } from '../constants.js'

export const startSession = async ({ data, xrSupported }) => {
  const { Clock } = await import('three/src/core/Clock.js')
  const { PerspectiveCamera } = await import('three/src/cameras/PerspectiveCamera.js')
  const { Scene } = await import('three/src/scenes/Scene.js')
  const { WebGLRenderer } = await import('three/src/renderers/WebGLRenderer.js')
  const { HemisphereLight } = await import('three/src/lights/HemisphereLight.js')

  let reticle = null

  const clock = new Clock()

  const scene = new Scene()

  const fov = 70
  const aspect = window.innerWidth / window.innerHeight
  const near = 0.01
  const far = 20

  const camera = new PerspectiveCamera(fov, aspect, near, far)

  const hemisphereLight = new HemisphereLight(0xffffbb, 0x080820, 10)
  scene.add(hemisphereLight)

  const renderer = new WebGLRenderer({ antialias: true, alpha: true })
  renderer.setPixelRatio(window.devicePixelRatio)
  renderer.setSize(window.innerWidth, window.innerHeight)
  renderer.domElement.id = 'three-canvas'
  renderer.gammaOutput = true
  renderer.shadowMap.enabled = true

  window.addEventListener('resize', onWindowResize({ camera, renderer }), false)

  const container = document.getElementById('three-container')

  container.appendChild(renderer.domElement)

  const { model, mixer } = await loadModel(data.file)

  if (xrSupported) {
    // only load Reticle if the scene needs it
    if (data.type === SCENE_TYPES.HIT) {
      reticle = await loadReticle()
      scene.add(reticle)
    }

    const session = await navigator.xr.requestSession('immersive-ar', config.xr.sessionConfig)

    // AR is supported, lets launch the session and scene.
    renderer.xr.enabled = true
    renderer.xr.setReferenceSpaceType('local')
    renderer.xr.setSession(session)

    const controller = renderer.xr.getController(0)

    const { type } = data

    if (type === SCENE_TYPES.HIT || type === SCENE_TYPES.FLOAT) {
      controller.addEventListener('select', spawnModel({ camera, controller, model, reticle, scene, type }))
    } else if (type === SCENE_TYPES.GPS) {
      // TODO
      // place the object without onSelect listener,
      // instead, get the gps location of the visitor and place the object right away,
      // positioned relative to that position
    } else if (type === SCENE_TYPES.IMAGE) {
      // TODO
      // draw a plane
      // texture with an image
      // make it possible to walk around it
    } else if (type === SCENE_TYPES.VIDEO) {
      // TODO
      // draw a plane
      // texture with a video
      // make it possible to walk around it
    }

    scene.add(controller)
  } else {
    // this is our fallback for non-ar devices.
    // instead of spawning an ar scene,
    // we will spawn a fallback scene that includes a 3d image of the place,
    // and OrbitControls to "move" the camera around that object.
    // this fallback will also be visible on the homepage without being at the position.

    spawnModel({ camera, model, scene })()

    // const { SphereBufferGeometry } = await import('three/src/geometries/SphereGeometry.js')
    // const { MeshBasicMaterial } = await import('three/src/materials/MeshBasicMaterial.js')

    // const geometry = new SphereBufferGeometry(5, 32, 32)
    // const material = new MeshBasicMaterial({ color: 0xffff00 })
    // const sphere = new THREE.Mesh(geometry, material)
    // scene.add(sphere)
  }

  const stats = await debugView({ camera, scene, hemisphereLight })

  renderer.setAnimationLoop(render({ camera, clock, mixer, renderer, reticle, scene, stats }))

  return scene
}
