import * as L from 'leaflet/dist/leaflet-src.esm.js'

import { data } from '../../../data.js'

import { zoomToLocation } from './zoomToLocation.js'
import { onClickStartScene } from '../../onClickStartScene.js'

export const addObjectsToLayer = ({ objects, objectsGroup, map, icon }) => {
  objects.forEach(slug => {
    const object = data.objects[slug]
    const { lat, lon, name, desc } = object

    const marker = L.marker([lat, lon], { icon, title: name })

    L.DomEvent.on(marker, 'dblclick', zoomToLocation, { location: object, map })

    const popupContent = L.DomUtil.create('div')

    const title = L.DomUtil.create('h3')
    title.innerHTML = name

    popupContent.appendChild(title)

    const description = L.DomUtil.create('p', 'description')
    description.innerHTML = desc

    popupContent.appendChild(description)

    const experienceButton = L.DomUtil.create('button', `expBtn-${object.slug}`)
    experienceButton.innerHTML = 'Start Experience'

    const eventContext = {
      object: {
        ...object,
        file: `models/${slug}.glb`,
      },
    }

    L.DomEvent.on(experienceButton, 'click', function (e) {
      onClickStartScene(this.object)
    }, eventContext)

    popupContent.appendChild(experienceButton)

    const popup = L.popup().setContent(popupContent)

    marker.bindPopup(popup)

    marker.addTo(objectsGroup)
  })
}
