let hitTestSource = null
let hitTestSourceRequested = false

export const render = state => async (timestamp, frame) => {
  const { camera, clock, mixer, renderer, reticle, scene, stats } = state
  if (frame) {
    const session = renderer.xr.getSession()

    if (hitTestSourceRequested === false) {
      const referenceSpace = await session.requestReferenceSpace('viewer')
      const source = await session.requestHitTestSource({ space: referenceSpace })
      hitTestSource = source

      session.addEventListener('end', () => {
        hitTestSourceRequested = false
        hitTestSource = null
      })

      hitTestSourceRequested = true
    }

    if (hitTestSource) {
      const referenceSpace = renderer.xr.getReferenceSpace()

      const hitTestResults = frame.getHitTestResults(hitTestSource)

      if (hitTestResults.length) {
        if (reticle) {
          // we get the first hit here,
          // instead, we should probably loop through them
          // and find the most probable hit match for our object.
          const hit = hitTestResults[0]

          reticle.visible = true
          reticle.matrix.fromArray(hit.getPose(referenceSpace).transform.matrix)
        }
      } else {
        if (reticle) {
          reticle.visible = false
        }
      }
    }
  }

  // update animation frames for all animations in this mixer
  if (mixer) {
    mixer.update(clock.getDelta())
  }

  stats.begin()
  renderer.render(scene, camera)
  stats.end()
}
