import * as L from 'leaflet/dist/leaflet-src.esm.js'

// all data is being imported statically,
// for now, we only deal with vienna.
import { data } from '../../../data.js'

import * as config from '../../../config.js'

import { zoomToLocation } from './zoomToLocation.js'
import { addObjectsToLayer } from './addObjectsToLayer.js'

export const addLocationsToLayer = map => {
  const locationIcon = L.icon(config.icons.location)
  const objectIcon = L.icon(config.icons.object)

  const locationsGroup = new L.FeatureGroup()
  const objectsGroup = new L.FeatureGroup()

  data.locations.forEach(location => {
    const { desc, lat, lon, name, objects = [] } = location

    const popupContent = L.DomUtil.create('div')

    const title = L.DomUtil.create('h3')
    title.innerHTML = name

    popupContent.appendChild(title)

    const description = L.DomUtil.create('p', 'description')
    description.innerHTML = desc
    popupContent.appendChild(description)

    const details = L.DomUtil.create('p', 'details')
    let num = objects.length
    if (num === 0) {
      num = 'are no Objects'
    } else if (num === 1) {
      num = 'is 1 Object'
    } else if (num > 1) {
      num = `are ${num} Objects`
    }
    details.innerHTML = `There ${num} at this location`
    popupContent.appendChild(details)

    const zoomBtn = L.DomUtil.create('button')
    zoomBtn.setAttribute('type', 'button')
    zoomBtn.innerHTML = 'Zoom to location'
    zoomBtn.className = `btn-${name.replace(/ /gim, '-')}`

    popupContent.appendChild(zoomBtn)

    const popup = L.popup().setContent(popupContent)

    L.DomEvent.on(zoomBtn, 'click', zoomToLocation, { location, map, close: false, btn: zoomBtn })

    const marker = L.marker([lat, lon], { icon: locationIcon, title: name })
    marker.bindPopup(popup)

    marker.addTo(locationsGroup)

    L.DomEvent.on(marker, 'dblclick', zoomToLocation, { location, map, close: false, btn: zoomBtn })

    addObjectsToLayer({ objects, objectsGroup, map, icon: objectIcon })
  })

  return {
    locationsGroup,
    objectsGroup,
  }
}
